import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { Button } from "@material-ui/core";
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Badge from "@material-ui/core/Badge";
import Grid from "@material-ui/core/Grid";
import { Modal } from 'react-bootstrap';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

import axios from 'axios'
import APIROUTE from '../Constants/ApiRoute'
import useAuth from 'stores/Author/AuthStore'
import 'bootstrap/dist/css/bootstrap.min.css';
import Modaljs from '../Admin_LuuTruXe/components/Modal'

import LinearProgress from '@material-ui/core/LinearProgress';
import CreditCardIcon from '@material-ui/icons/CreditCard';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import Input from '@material-ui/core/Input';
import AirplanemodeInactiveOutlinedIcon from '@material-ui/icons/AirplanemodeInactiveOutlined';
import AirplanemodeActiveOutlinedIcon from '@material-ui/icons/AirplanemodeActiveOutlined';
import { API_HOST } from 'config/config';

import DateFnsUtils from '@date-io/date-fns';
import 'date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';



const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});

export default function QuanLyXe() {

  const classes = useStyles();
  const [posts, setPosts] = useState([]);
  const [isRefresh, setIsRefresh] = useState(false);
  const [stateAuth,] = useAuth();
  const [openloading, setOpenloading] = useState(false);
  const [modalAddPlateShow, setmodalAddPlateShow] = useState(false);
  const [nbPlateUpdate,setNbPlateUpdate] = useState("");
  const [IDVehicle, setIDVehicle] = useState("");

  const [color, setColor] = useState("");
  const [brand, setBrand] = useState("");
  const [numberPlate, setNumberPlate] = useState("");
  const [DateNSX, setDateNSX] = useState(new Date('2014-08-18T21:11:54'));
  const [DateNDK, setDateNDK] = useState(new Date('2014-08-18T21:11:54'));

  const handleDateNSXChange = (date) => {
    setDateNSX(date);
  };
  const handleDateNDKChange = (date) => {
    setDateNDK(date);
  };
  const handleColorChange = (event) => {
    setColor(event.target.value);
  };
  const handleBrandChange = (event) => {
    setBrand(event.target.value);
  };
  const handlePlateChange = (event) => {
    setNumberPlate(event.target.value);
  };
  const handleNMPlateUpdateChange = (event) => {
    setNbPlateUpdate(event.target.value);
  };



  const onHandleApproval = async (IDBanGhi,NumberPlate) => {
    setOpenloading(true);
    var data = JSON.stringify({ "numberPlate": NumberPlate });
    var config = {
      method: 'put',
      url: `${API_HOST}${APIROUTE.VEHICLES}/${IDBanGhi}`,
      headers: {
        'Authorization': 'Bearer ' + stateAuth.accessToken,
        'Content-Type': 'application/json',
        'Cookie': 'ARRAffinity=b6c882a5e013db277ef434fb044e8255f520bbdc70059876373bab098d534452; ARRAffinitySameSite=b6c882a5e013db277ef434fb044e8255f520bbdc70059876373bab098d534452'
      },
      data: data
    };
    await axios(config).then(() => setIsRefresh(!isRefresh))
      .catch(err => console.log(err));

    setOpenloading(false);
  }
  const onHandleDisable = async (IDBanGhi, Isactive) => {
    setOpenloading(true);
    var data = JSON.stringify({ "isActive": !Isactive });
    var config = {
      method: 'put',
      url: `${API_HOST}${APIROUTE.VEHICLES}/${IDBanGhi}`,
      headers: {
        'Authorization': 'Bearer ' + stateAuth.accessToken,
        'Content-Type': 'application/json',
        'Cookie': 'ARRAffinity=b6c882a5e013db277ef434fb044e8255f520bbdc70059876373bab098d534452; ARRAffinitySameSite=b6c882a5e013db277ef434fb044e8255f520bbdc70059876373bab098d534452'
      },
      data: data
    };
    axios(config).then(() => setIsRefresh(!isRefresh))
      .catch(err => console.log(err));

    setOpenloading(false);
  }

  const onHandleAddvehicle = async () => {
    setOpenloading(true);
    await axios.post(`${API_HOST}${APIROUTE.VEHICLES}`, {
      headers: {
        Authorization: 'Bearer ' + stateAuth.accessToken
      },
      "frameNumber": "SK-999999",
      "color": color,
      "productionDate": DateNSX,
      "registrationDate": DateNDK,
      "brand": brand,
      "vehicleTypeId": 1,
      "numberPlate": numberPlate,
      "note": "new vehicle",
      "capacity": "2",
      "engineIDNo": "SM1999999"
    }).then(() => setIsRefresh(!isRefresh))
      .catch(err => console.log(err));
    setOpenloading(false);
  }

  useEffect(() => {
    (async () => {
      setOpenloading(true)
      await axios.get(`${API_HOST}${APIROUTE.VEHICLES}`, {
        headers: {
          Authorization: 'Bearer ' + stateAuth.accessToken
        }
      })
        .then(res => {
          setPosts(res.data);
        })
        .catch(err => console.log(err))
      setOpenloading(false)
    })()
    // eslint-disable-next-line
  }, [isRefresh])

  return (
    <Grid container spacing={3}>
      <TableContainer component={Paper}>
        {openloading ? <LinearProgress color="secondary" /> : null}
        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell align="center">ID Xe</TableCell>
              <TableCell align="center">Hãng</TableCell>
              <TableCell align="center">Biển Số</TableCell>
              <TableCell align="center">Màu sơn</TableCell>
              <TableCell align="center">Ngày Sản Xuất</TableCell>
              <TableCell align="center">Ngày Đăng Kí</TableCell>
              <TableCell align="center">Trạng thái</TableCell>
              <TableCell align="center">Xử lý</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            <TableRow>
              <TableCell align="center"></TableCell>
              <TableCell align="center"><Input name='brand' onChange={handleBrandChange}></Input></TableCell>
              <TableCell align="center"><Input name='numberPlate' onChange={handlePlateChange}></Input></TableCell>
              <TableCell align="center"><Input name='color' onChange={handleColorChange}></Input></TableCell>
              <TableCell align="center">
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    disableToolbar
                    variant="inline"
                    format="MM/dd/yyyy"
                    margin="normal"
                    id="date-picker-inline"
                    value={DateNSX}
                    onChange={handleDateNSXChange}
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                  />
                </MuiPickersUtilsProvider>
              </TableCell>
              <TableCell align="center">
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    disableToolbar
                    variant="inline"
                    format="MM/dd/yyyy"
                    margin="normal"
                    id="date-picker-inline"
                    value={DateNDK}
                    onChange={handleDateNDKChange}
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                  />
                </MuiPickersUtilsProvider>
              </TableCell>
              <TableCell align="center"></TableCell>
              <TableCell align="center">
                <Button
                  variant="outlined" color="primary" className={classes.button}
                  onClick={onHandleAddvehicle}
                ><AddCircleOutlineIcon />
                </Button>
              </TableCell>

            </TableRow>
            {posts.map((post) => (
              <TableRow key={post.id}>
                <TableCell align="center">
                  <Badge badgeContent={post.id} max={999} color="primary"></Badge>
                </TableCell>
                <TableCell align="center">{post.brand}</TableCell>
                <TableCell align="center">{post.numberPlate}</TableCell>
                <TableCell align="center">{post.color}</TableCell>
                <TableCell align="center">{post.productionDate} </TableCell>
                <TableCell align="center">{post.registrationDate}</TableCell>
                {post.isActive === true ? (
                  <TableCell align="center">
                    <Badge badgeContent='Circulate' color="primary"></Badge>
                  </TableCell>
                ) : (
                    <TableCell align="center">
                      <Badge badgeContent='Uncirculate' color="error"></Badge>
                    </TableCell>
                  )}

                <TableCell>
                  <ButtonGroup variant="text" color="primary" aria-label="text primary button group">

                    <Button onClick={() => onHandleDisable(post.id, post.isActive)} >
                      {post.isActive === true ? (
                        <AirplanemodeInactiveOutlinedIcon color="disabled" />
                      ) : (
                          <AirplanemodeActiveOutlinedIcon color="action" />
                        )}
                    </Button>
                    <Button
                      onClick={() => {
                        setmodalAddPlateShow(true)
                        setIDVehicle(post.id)
                      }}
                    ><CreditCardIcon /></Button>
                  </ButtonGroup>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Modaljs
        header={"Thêm biển số"}
        show={modalAddPlateShow}
        onHide={() => {
          setmodalAddPlateShow(false);
          setIsRefresh(!isRefresh);
        }}
      >
        <Modal.Body>
          <div className="container">           
            <div className="row">Vui lòng nhập biển số xe </div>
            <div className="row">
              <Autocomplete
                id="combo-box-demo"
                options={[
                  {plate : "59A-18082"},
                  {plate : "59A-18083"},
                  {plate : "59A-18084"},
                ]}
                getOptionLabel={(option) => option.plate}
                onClose={handleNMPlateUpdateChange}
                style={{ width: 900 }}
                renderInput={(params) => <TextField {...params} label="Biển số" variant="outlined"
                />}
              />
            </div>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button
            onClick={()=>{
              onHandleApproval(IDVehicle,nbPlateUpdate)
              setmodalAddPlateShow(false)
            }}
          >Xác nhận cập nhật</Button>
        </Modal.Footer>
      </Modaljs>

    </Grid>
  );
}