import React, { useState, useEffect } from "react";

import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { Button } from "@material-ui/core";
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Badge from "@material-ui/core/Badge";
import Grid from "@material-ui/core/Grid";
import { Modal } from 'react-bootstrap';

import axios from 'axios'
import APIROUTE from '../Constants/ApiRoute'
import useAuth from 'stores/Author/AuthStore'
import 'bootstrap/dist/css/bootstrap.min.css';
import Modaljs from './components/Modal'
import CapNhatBanGhi from './Admin_CapNhatBanGhi';
import DuyetHoSo from './Admin_DuyetHoSo';

import LinearProgress from '@material-ui/core/LinearProgress';
import BuildOutlinedIcon from '@material-ui/icons/BuildOutlined';
import DeleteForeverOutlinedIcon from '@material-ui/icons/DeleteForeverOutlined';

import { API_HOST } from 'config/config'


const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});


const LuuTruXe = () => {

  const classes = useStyles();
  const [posts, setPosts] = useState([]);
  const [isRefresh, setIsRefresh] = useState(false);
  const [stateAuth,] = useAuth();
  const [openloading, setOpenloading] = useState(false);
  const [IDBanGhi, setIDBanGhi] = useState(0);
  const [modalUpdateShow, setmodalUpdateShow] = useState(false);
  const [modalDuyetHS, setmodalDuyetHS] = useState(false);

  const onHandleApproval = async(IDBanGhi) => {
    await axios.get(`${API_HOST}${APIROUTE.RECORD_PROFILE}${APIROUTE.APPROVAL}/${IDBanGhi}`, {
      headers: {
        Authorization: 'Bearer ' + stateAuth.accessToken  // Gửi token lên để xác nhận người dùng.
      }
    })
      .then(() => {
        setmodalDuyetHS(false)
        setIsRefresh(!isRefresh)
      })
      .then(err => console.log(err))
  }

  const onHandleDelete = async(IDBAnGhiXoa) => {
    await axios.delete(`${API_HOST}${APIROUTE.RECORD_PROFILE}/${IDBAnGhiXoa}`, {
      headers: {
        Authorization: 'Bearer ' + stateAuth.accessToken  // Gửi token lên để xác nhận người dùng.
      }
    })
  }

 

  useEffect(() => {
    (async () => {
      setOpenloading(true)
      await axios.get(`${API_HOST}${APIROUTE.RECORD_PROFILE}`, {
        headers: {
          Authorization: 'Bearer ' + stateAuth.accessToken
        }
      })
        .then(res => {
          setPosts(res.data);
        })
        .catch(err => console.log(err))
      setOpenloading(false)
    })()
    // eslint-disable-next-line
  }, [isRefresh])



  return (
    <Grid container spacing={3}>
      <TableContainer component={Paper}>
      {openloading ? <LinearProgress color="secondary" /> : null}
        <Table className={classes.table} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell align="center">ID bản ghi</TableCell>
              <TableCell align="center">Loại Xe</TableCell>
              <TableCell align="center">Biển Số</TableCell>
              <TableCell align="center">Tên chủ xe</TableCell>
              <TableCell align="center">Ngày Tạo</TableCell>
              <TableCell align="center">Ngày Cập Nhật</TableCell>
              <TableCell align="center">Trạng thái</TableCell>
              <TableCell align="center">Xử lý</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {posts.map((post) => (
              <TableRow key={post.id}>
                <TableCell align="center">
                  <Badge badgeContent={post.id} max={999} color="primary"></Badge>
                </TableCell>
                <TableCell align="center">{post.brand}</TableCell>
                <TableCell align="center">{post.numberPlate}</TableCell>
                <TableCell align="center">{post.firstName} {post.lastName}</TableCell>
                <TableCell align="center">{post.createDate} </TableCell>
                <TableCell align="center">{post.updatedDate}</TableCell>
                {post.isActive === true ? (
                  <TableCell align="center">
                    <Badge badgeContent='Approved' color="primary"></Badge>
                  </TableCell>
                ) : (
                    <TableCell align="center">
                      <Badge badgeContent='Unapproved' color="error"></Badge>
                    </TableCell>
                  )}
                {post.isActive === false ? (
                  <TableCell>
                    <ButtonGroup variant="text" color="primary" aria-label="text primary button group">
                      <Button onClick={() => {
                          onHandleDelete(post.id)
                          setIsRefresh(!isRefresh);
                        }}><DeleteForeverOutlinedIcon/></Button>

                      <Button onClick={() => {
                        setIDBanGhi(post.id)
                        setmodalUpdateShow(true)
                      }}> <BuildOutlinedIcon/></Button>

                      <Button onClick={() => {
                        setIDBanGhi(post.id)
                        setmodalDuyetHS(true)
                      }}><span class="material-icons">done_outline</span></Button>
                    </ButtonGroup>

                  </TableCell>
                ) : null}

              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Modaljs
        IDBanGhi={IDBanGhi}
        header={"Cập nhật hồ sơ"}
        show={modalUpdateShow}
        onHide={() => {
          setmodalUpdateShow(false);
          setIsRefresh(!isRefresh);
        }}
      >
        <CapNhatBanGhi IDBanGhi={IDBanGhi} />
      </Modaljs>

      <Modaljs
        IDBanGhi={IDBanGhi}
        header={"Vui lòng kiểm tra lại thông tin hồ sơ trước khi duyệt"}
        show={modalDuyetHS}
        onHide={() => setmodalDuyetHS(false)}
      >
        <Modal.Body>
          <DuyetHoSo IDBanGhi={IDBanGhi} />
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={() => onHandleApproval(IDBanGhi)}>Duyệt Hồ Sơ</Button>
        </Modal.Footer>
      </Modaljs>
    </Grid>
  );
};

export default LuuTruXe;
