import React, { useState, useEffect } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';

import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

import axios from 'axios'
import APIROUTE from '../Constants/ApiRoute'
import Loading from './Loading'
import useAuth from 'stores/Author/AuthStore'
import { API_HOST } from 'config/config'
import { Modal } from 'react-bootstrap';
import { Button } from "@material-ui/core";


const CapNhatBanGhi = ({ IDBanGhi }) => {
  const [openloading, setOpenloading] = useState(false);
  const [allVehicles, setAllVehicles] = useState([]);
  const [stateAuth,] = useAuth();
  const [IdVehicle, setIdVehicle] = useState();

  useEffect(() => {
    (async () => {
      setOpenloading(true);
      await axios.get(`${API_HOST}${APIROUTE.VEHICLES}`, {
        headers: {
          Authorization: 'Bearer ' + stateAuth.accessToken
        }
      })
        .then(res => {
          setAllVehicles(res.data);
        })
        .catch(err => console.log(err))
      setOpenloading(false);
    })()
  }
    // eslint-disable-next-line
    , [null])

  const onIdVehicleChange = (event) => {
    setIdVehicle(parseInt(event.target.value));
  }

  const onHandleUpdate = async(IDUpdate) => {
    await axios.put(`${API_HOST}${APIROUTE.RECORD_PROFILE}/${IDUpdate}`, {
       headers: {
         Authorization: 'Bearer ' + stateAuth.accessToken  // Gửi token lên để xác nhận người dùng.
       },
       "vehicleId": IdVehicle
     })
       .catch(err => console.log(err))
 }

  return (
    <div>
      <Modal.Body>
        <div className="container">
          <Loading open={openloading} />
          <div className="row">Vui lòng chọn biển số xe</div>
          <div className="row">
            <Autocomplete
              id="combo-box-demo"
              options={allVehicles}
              getOptionLabel={(option) => String(option.id)}   
              renderOption={(option) => <span>{option.brand} -- {option.numberPlate}</span>}
              onClose={onIdVehicleChange}                  
              style={{ width: 900 }}
              renderInput={(params) => <TextField {...params} label="Biển số" variant="outlined"  
              />}
            />
          </div>
        </div>
      </Modal.Body>
      <Modal.Footer>   
        <Button
          onClick={() => onHandleUpdate(IDBanGhi)}
        >Xác nhận cập nhật</Button>
      </Modal.Footer>
    </div>
  );
};

export default CapNhatBanGhi;
